import React, { type ButtonHTMLAttributes, type FC, Fragment } from 'react';
import { Listbox, Transition } from '@headlessui/react';
import type { VariantProps } from 'class-variance-authority';
import { inputVariants } from '../../variants/input-variants';
import Avatar from '../../avatar/Avatar';
import Loader from '../../loader/Loader';
import { ReIcon } from '@/components/ReIcon/ReIcon';
import Tag from '../../tag/Tag';

interface ListboxItem {
  id?: string;
  name: string;
  icon?: string;
  image?: string;
}

export interface ListboxProps
  extends Omit<ButtonHTMLAttributes<HTMLButtonElement>, 'size' | 'color'>,
    VariantProps<typeof inputVariants> {
  label?: string;
  multiple?: boolean;
  error?: string;
  value?: any;
  selected?: any;
  setSelected?: any;
  disabled?: boolean;
  items?: ListboxItem[] | any[];
  classNames?: string;
  loading?: boolean;
}

const ListBox: FC<ListboxProps> = ({
  value,
  selected,
  setSelected,
  label,
  multiple,
  size = 'md',
  color = 'default',
  shape = 'smooth',
  disabled,
  items = [],
  classNames,
  loading = false,
  error,
  ...props
}) => {
  return (
    <div className={`relative w-full ${classNames}`}>
      <Listbox value={selected} onChange={setSelected} multiple={multiple}>
        <div className="relative z-0 w-full font-sans">
          {!!label && <label className="font-sans text-[.85rem] text-muted-400">{label}</label>}
          <Listbox.Button
            className={inputVariants({
              size,
              color,
              shape,
              className: `group/listbox-button relative text-start
              ${size === 'sm' && selected?.icon ? 'pe-2 ps-8' : ''}
              ${size === 'md' && selected?.icon ? 'pe-3 ps-10' : ''}
              ${size === 'lg' && selected?.icon ? 'pe-4 ps-12' : ''}
              ${size === 'sm' && selected?.image ? 'pe-2 ps-8' : ''}
              ${size === 'md' && selected?.image ? 'pe-3 ps-11' : ''}
              ${size === 'lg' && selected?.image ? 'pe-4 ps-12' : ''}
              ${size === 'sm' && !selected?.icon ? 'px-2' : ''}
              ${size === 'md' && !selected?.icon ? 'px-3' : ''}
              ${size === 'lg' && !selected?.icon ? 'px-4' : ''}
              ${size === 'sm' && !selected?.image ? 'px-2' : ''}
              ${size === 'md' && !selected?.image ? 'px-3' : ''}
              ${size === 'lg' && !selected?.image ? 'px-4' : ''}
              ${error ? '!border-danger-500' : ''}
              ${disabled ? '!pointer-events-none !cursor-not-allowed !opacity-50' : ''}
              ${loading ? '!text-transparent !pointer-events-none !select-none' : ''}
            `
            })}
            {...props}
          >
            <span className="block truncate">{selected?.name}</span>

            <div
              className={`absolute start-0 top-0 z-0 flex items-center justify-center text-muted-400 transition-colors duration-300 peer-focus-visible:text-primary-500 dark:text-muted-500 
                ${size === 'sm' ? 'h-8 w-8' : ''} 
                ${size === 'md' ? 'h-10 w-10' : ''} 
                ${size === 'lg' ? 'h-12 w-12' : ''}`}
            >
              {!!selected?.icon && !selected?.image ? (
                <ReIcon
                  iconName={selected?.icon}
                  className={`
                    ${size === 'sm' ? 'h-3 w-3' : ''} 
                    ${size === 'md' ? 'h-4 w-4' : ''} 
                    ${size === 'lg' ? 'h-5 w-5' : ''}
                    ${error ? '!text-danger-500' : ''}
                  `}
                />
              ) : (
                ''
              )}
              {!!selected?.image && !selected?.icon ? (
                <Avatar
                  src={selected?.image}
                  text={selected?.name.substring(0, 1)}
                  size={size === 'lg' ? 'xxs' : 'xxxs'}
                />
              ) : (
                ''
              )}
            </div>
            {!!loading ? (
              <div
                className={`absolute end-0 top-0 z-0 flex items-center justify-center text-muted-400 transition-colors duration-300 peer-focus-visible:text-primary-500 dark:text-muted-500 
                  ${size === 'sm' ? 'h-8 w-8' : ''} 
                  ${size === 'md' ? 'h-10 w-10' : ''} 
                  ${size === 'lg' ? 'h-12 w-12' : ''}`}
              >
                <Loader
                  classNames={`dark:text-muted-200
                ${
                  color === 'muted' || color === 'mutedContrast'
                    ? 'text-muted-400'
                    : 'text-muted-300'
                }
              `}
                  size={20}
                  thickness={4}
                />
              </div>
            ) : (
              ''
            )}
            <span
              className={`pointer-events-none absolute end-0 top-0 flex items-center justify-center
                ${size === 'sm' ? 'h-8 w-8' : ''} 
                ${size === 'md' ? 'h-10 w-10' : ''} 
                ${size === 'lg' ? 'h-12 w-12' : ''}
                ${loading ? '!text-transparent !opacity-0' : ''}
              `}
            >
              <ReIcon
                iconName="BsChevronExpand"
                className="h-4 w-4 text-muted-400 transition-transform duration-300 group-focus/listbox-button:rotate-180"
                aria-hidden="true"
              />
            </span>
          </Listbox.Button>
          <Transition
            as={Fragment}
            leave="transition ease-in duration-100"
            leaveFrom="opacity-100"
            leaveTo="opacity-0"
          >
            <Listbox.Options
              className={`slimscroll absolute z-0 mt-1 w-full overflow-auto border !p-2 text-base shadow-lg shadow-muted-300/30 ring-1 ring-primary-500 ring-opacity-5 focus:outline-none dark:shadow-muted-800/20 sm:text-sm
                ${selected?.image ? 'max-h-[255px]' : 'max-h-60'}
                ${shape === 'rounded' ? 'rounded-md' : ''}
                ${shape === 'smooth' ? 'rounded-lg' : ''}
                ${shape === 'rounded' ? 'rounded-md' : ''}
                ${shape === 'curved' ? 'rounded-xl' : ''}
                ${shape === 'full' ? 'rounded-xl' : ''}
                ${
                  color === 'default'
                    ? 'border-muted-200 bg-white dark:border-muted-700 dark:bg-muted-800'
                    : ''
                }
                ${
                  color === 'contrast'
                    ? 'border-muted-200 bg-white dark:border-muted-800 dark:bg-muted-950'
                    : ''
                }
                ${
                  color === 'muted'
                    ? 'border-muted-200 bg-white dark:border-muted-700 dark:bg-muted-800'
                    : ''
                }
                ${
                  color === 'mutedContrast'
                    ? 'border-muted-200 bg-white dark:border-muted-800 dark:bg-muted-950'
                    : ''
                }
              `}
            >
              {items.map((item, itemIdx) => (
                <Listbox.Option
                  key={itemIdx}
                  className={({ active }) =>
                    `relative flex select-none items-center gap-2 p-2 transition-colors duration-300 cursor-pointer 
                  ${
                    active
                      ? 'bg-primary-500/10 text-primary-700 dark:bg-primary-500/20'
                      : 'text-muted-600 dark:text-muted-400'
                  }
                  ${shape === 'rounded' ? 'rounded-md' : ''}
                  ${shape === 'smooth' ? 'rounded-lg' : ''}
                  ${shape === 'rounded' ? 'rounded-md' : ''}
                  ${shape === 'curved' ? 'rounded-xl' : ''}
                  ${shape === 'full' ? 'rounded-xl' : ''}
                  `
                  }
                  value={item}
                >
                  {({ selected }) => {
                    return (
                      <>
                        {!!item?.icon && !item?.image ? (
                          <span className={`pointer-events-none flex items-center justify-center`}>
                            <ReIcon
                              iconName={item?.icon}
                              className="h-5 w-5 text-muted-400 transition-colors duration-300"
                              aria-hidden="true"
                            />
                          </span>
                        ) : (
                          ''
                        )}
                        {!!item?.image && !item?.icon ? (
                          <Avatar src={item?.image} text={item?.name.substring(0, 1)} size="xxs" />
                        ) : (
                          ''
                        )}
                        <span className={`block truncate text-sm`}>{item.name}</span>
                        {item.count && (
                          <Tag
                            variant="solid"
                            shape="smooth"
                            color="primary"
                            className="relative z-0 ms-auto flex items-center"
                          >
                            {item.count}
                          </Tag>
                        )}
                        {/* {selected ? (
                          <span className="relative z-0 ms-auto flex items-center pe-2 text-primary-600">
                            <ReIcon
                              iconName="AiOutlineCheck"
                              className="h-4 w-4"
                              aria-hidden="true"
                            />
                          </span>
                        ) : null} */}
                      </>
                    );
                  }}
                </Listbox.Option>
              ))}
            </Listbox.Options>
          </Transition>
        </div>
      </Listbox>
    </div>
  );
};

export default ListBox;
